class catalog extends Highway.Renderer {
    onEnter() {

        var elms = document.getElementsByClassName( 'js-slider' );
        for ( var i = 0, len = elms.length; i < len; i++ ) {
            new Splide(elms[ i ], {
                perPage: 4,
                perMove: 2,
                gap    : '5rem',
                arrows: false,
                pagination: false,
            
                breakpoints: {
                    '700': {
                        perPage: 2,
                        gap    : '1rem',
                    }
                }
            } ).mount();
        }


        //SEARCH

        let bar = document.getElementById("js-cat_search");

        let all_sec = document.getElementsByClassName("js-cat-section");

        function hide_all_sections(){
            for ( var i = 0; i < all_sec.length; i++ ) {
                all_sec[i].style.opacity = ".5";
            }
        }

        bar.addEventListener("input", function(){
            let val = bar.value.toLowerCase();

            if(val.includes("hoodie")){
                hide_all_sections();
                document.getElementById("shirts").scrollIntoView();
                document.getElementById("shirts").style.opacity = "";
            }

            else if(val.includes("shirt")){
                hide_all_sections();
                document.getElementById("outwear").scrollIntoView();
                document.getElementById("outwear").style.opacity = "";
            }

            else{
                for ( var i = 0; i < all_sec.length; i++ ) {
                    all_sec[i].style.opacity = "";
                }
            }
        })




        document.getElementById("js-search-show").addEventListener("click", function(){
            document.getElementById("js-nav").classList.toggle("js-search");
            bar.focus();
        })

        document.getElementById("js-search-hide").addEventListener("click", function(){
            document.getElementById("js-nav").classList.toggle("js-search");
            bar.value="";
            for ( var i = 0; i < all_sec.length; i++ ) {
                all_sec[i].style.opacity = "";
            }
        })
    }
}

export default catalog;