class product extends Highway.Renderer {
    onEnter() {
        let item = document.getElementById("js-item"),
        item_show = document.getElementById("js-item-show"),
        item_hide = document.getElementById("js-item-hide");


        item_show.addEventListener("click", function(){
            item.style.display="block";
        })
        
        item_hide.addEventListener("click", function(){
            item.style.display="";
        })




    let size_selector = document.querySelectorAll("#js-size-select > button");

    for (var i=0; i < size_selector.length; i++) {
        size_selector[i].addEventListener("click", function(){
            document.querySelector(".active-size").classList.remove("active-size");
            this.classList.add("active-size");
        })
    }



    //PRODUCT HOODIE SIZE DETAILS

    let hoodie_shoulder_size = ['22.83 "', '23.62 "', '24.41 "', '25.20 "', '26.18 "', '58 cm', '60 cm', '62 cm', '64 cm', '66.5 cm'],
        hoodie_arm_size = ['24.41 "', '25.20 "', '25.98 "', '26.77 "', '27.56 "', '62 cm', '64 cm', '66 cm', '68 cm', '70 cm'],
        hoodie_chest_size = ['24.41 "', '25.20 "', '26.38 "', '27.56 "', '29.13 "', '62 cm', '64 cm', '67 cm', '70 cm', '74 cm'],
        hoodie_torso_size = ['25.98 "', '26.77 "', '27.56 "', '27.95 "', '28.54 "', '66 cm', '68 cm', '70 cm', '71 cm', '72.5 cm'];


    //PRODUCT T-SHIRT SIZE DETAILS

    let tee_shoulder_size = ['20.47 "', '20.47 "', '20.87 "', '21.26 "', '21.65 "', '52 cm', '52 cm', '53 cm', '54 cm', '55 cm'],
        tee_arm_size = ['5.91 "', '5.91 "', '6.30 "', '6.69 "', '7.09 "', '15 cm', '16 cm', '16 cm', '17 cm', '18 cm'],
        tee_torso_size = ['28.35 "', '28.35 "', '28.74 "', '29.13 "', '29.53 "', '72 cm', '72 cm', '73 cm', '74 cm', '75 cm'];



    //GENERAL SIZE CODE

    let size_btn = document.querySelectorAll("#js-size-trigger > li");

    let male_size = ['38.00"', '34.00"', '17.00"', '31.00"', "96.52 cm", "86.36 cm", "43.18 cm", "78.74 cm"];
    let female_size = ['32.00"', '34.00"', '14.00"', '23.00"', "81.28 cm", "86.36 cm", "35.56 cm", "58.42 cm"];

    let shoulder = document.querySelector("#js-shoulder");
    let chest = document.querySelector("#js-chest");
    let arm = document.querySelector("#js-arm");
    let torso = document.querySelector("#js-torso");
    let male = document.querySelectorAll("#js-male-model-dim > li > span");
    let female = document.querySelectorAll("#js-female-model-dim > li > span");
    let inch = document.querySelector("#js-inch");
    let cm = document.querySelector("#js-cm");
    let male_height = document.querySelector("#js-male-model-height");
    let female_height = document.querySelector("#js-female-model-height");
    let type = item.getAttribute("data-item");
    let unit = "i";

    inch.addEventListener("click", function(){
        this.classList.add("active-unit");
        cm.classList.remove("active-unit");

        for (var i=0; i < 4; i++){
            male[i].innerHTML = male_size[i];
            female[i].innerHTML = female_size[i];
        }

        male_height.innerHTML = '6 ft 2"';
        female_height.innerHTML = '5 ft 8"';

        dims_change("i", document.querySelector(".active-size").getAttribute("data-size"));

        unit = "i";
    })

    cm.addEventListener("click", function(){

        this.classList.add("active-unit");
        inch.classList.remove("active-unit");

        for (var i=4; i < 8; i++){
            male[i-4].innerHTML = male_size[i];
            female[i-4].innerHTML = female_size[i];
        }

        male_height.innerHTML = '188 cm';
        female_height.innerHTML = '173 cm';

        dims_change("c", document.querySelector(".active-size").getAttribute("data-size"));

        unit = "c";
    })

    for (var i=0; i < size_btn.length; i++) {
        size_btn[i].addEventListener("click", function(){
            document.querySelector(".active-size").classList.remove("active-size");
            this.classList.add("active-size");
            dims_change(unit, this.getAttribute("data-size"));
        })
    }

    function size_change(i){
        if(type=="hood"){
            shoulder.innerHTML = hoodie_shoulder_size[i];
            chest.innerHTML = hoodie_chest_size[i];
            arm.innerHTML = hoodie_arm_size[i];
            torso.innerHTML = hoodie_torso_size[i];
        }
        else if(type=="tee"){
            shoulder.innerHTML = tee_shoulder_size[i];
            //chest.innerHTML = tee_chest_size[i];
            arm.innerHTML = tee_arm_size[i];
            torso.innerHTML = tee_torso_size[i];
        }
    }
    function dims_change(unit, size){
        if(unit == "i"){
            if(size=="xs"){size_change(0)}
            else if(size=="s"){size_change(1)}
            else if(size=="m"){size_change(2)}
            else if(size=="l"){size_change(3)}
            else if(size=="xl"){size_change(4)}
        }
        else{
            if(size=="xs"){size_change(5)}
            else if(size=="s"){size_change(6)}
            else if(size=="m"){size_change(7)}
            else if(size=="l"){size_change(8)}
            else if(size=="xl"){size_change(9)}
        }
        
    }
    }
  }




  export default product;